import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Descritivo_treinamento } from '../../../modelo/Descritivo_treinamento'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'descritivo_treinamento-dados-details',   templateUrl: 'descritivo_treinamento_dadosDetails.component.html' }) export class Descritivo_treinamentoDadosDetailsComponent implements OnInit {    descritivo_treinamento: Descritivo_treinamento;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.descritivo_treinamento.Descritivo_treinamento";                 let id = +params['id'];         this.descritivo_treinamento = new Descritivo_treinamento();         this.descritivo_treinamento.cd_descritivo_treinamento = id;         o.objetoJson = this.descritivo_treinamento;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a descritivo_treinamento. '; }      this.descritivo_treinamento = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


